}
#chatter ul.discussions li a,
#chatter ul.discussions li .chatter_posts {
  background: #ffffff;
  padding: 15px;
  cursor: pointer;
  transition: background 0.3s ease;
  border-top: 1px solid #f1fcff;
  text-decoration: none;
  display: block;
}
#chatter ul.discussions li a .chatter_avatar,
#chatter ul.discussions li .chatter_posts .chatter_avatar {
