}
#chatter ul.discussions li a .chatter_middle,
#chatter ul.discussions li .chatter_posts .chatter_middle {
  float: left;
  margin-left: 90px;
  margin-right: 90px;
}
#chatter ul.discussions li a .chatter_middle h3,
#chatter ul.discussions li .chatter_posts .chatter_middle h3 {
