}
#chatter ul.discussions li .chatter_posts .chatter_middle > span {
  font-weight: bold;
  display: block;
  padding-bottom: 10px;
}
#chatter ul.discussions li .chatter_posts .chatter_middle > span a {
  font-weight: bold;
  color: #131619;
  font-size: 14px;
}
#chatter ul.discussions li .chatter_posts .chatter_middle > span .ago {
  font-size: 14px;
  color: #7089a9;
  padding-left: 6px;
  font-weight: 400;
}
#chatter ul.discussions li .chatter_posts .chatter_middle a {
  display: inline-block;
  background: none;
  padding: 0;
  text-decoration: none;
  border-top: 0px;
}
#chatter ul.discussions li .chatter_posts .chatter_middle p {
