  padding-right: 10px;
}
#chatter .chatter-alert {
  width: 100%;
  margin-bottom: 0px;
  border-radius: 0px;
}
#chatter .chatter-alert.alert-success {
  background: #55B559;
  border: 0px;
  color: #fff;
}
#chatter .chatter-alert.alert-info {
  background: #0ab1fc;
  border: 0px;
  color: #fff;
}
#chatter .chatter-alert.alert-warning {
  background: #FBC53C;
  border: 0px;
  color: #fff;
}
#chatter .chatter-alert.alert-error {
