  cursor: pointer;
}
#chatter .chatter_sidebar > a {
  padding: 20px;
  display: block;
}
#chatter .chatter_sidebar > a:hover {
  text-decoration: none;
}
#chatter .chatter_sidebar ul {
  margin-left: 10px;
}
#chatter .chatter_loader_container {
  background: #fff;
  border-radius: 3px;
  padding: 2px;
  width: 100%;
}
#chatter .chatter_loader,
#chatter .chatter_loader > div {
  position: relative;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
#chatter .chatter_loader {
  display: block;
  font-size: 0;
  margin: 20px auto;
  color: #fff;
}
#chatter .chatter_loader.loaded {
  display: none;
}
#chatter .chatter_loader.dark {
  color: #0098cb;
}
#chatter .chatter_loader > div {
  display: inline-block;
  float: none;
  background-color: currentColor;
  border: 0 solid currentColor;
}
#chatter .chatter_loader {
  width: 32px;
  height: 32px;
}
#chatter .chatter_loader > div {
  width: 32px;
  height: 32px;
  background: transparent;
  border-width: 2px;
  border-bottom-color: transparent;
  border-radius: 100%;
  -webkit-animation: ball-clip-rotate 0.75s linear infinite;
  -moz-animation: ball-clip-rotate 0.75s linear infinite;
  -o-animation: ball-clip-rotate 0.75s linear infinite;
  animation: ball-clip-rotate 0.75s linear infinite;
}
#chatter .chatter_loader.la-sm {
  width: 16px;
  height: 16px;
}
#chatter .chatter_loader.la-sm > div {
  width: 16px;
  height: 16px;
  border-width: 1px;
}
#chatter .chatter_loader.la-2x {
  width: 64px;
  height: 64px;
}
#chatter .chatter_loader.la-2x > div {
  width: 64px;
  height: 64px;
  border-width: 4px;
}
#chatter .chatter_loader.la-3x {
  width: 96px;
  height: 96px;
}
#chatter .chatter_loader.la-3x > div {
  width: 96px;
  height: 96px;
  border-width: 6px;
}
@-webkit-keyframes ball-clip-rotate {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-moz-keyframes ball-clip-rotate {
  0% {
    -moz-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  50% {
    -moz-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  100% {
    -moz-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-o-keyframes ball-clip-rotate {
  0% {
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  50% {
    -o-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  100% {
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes ball-clip-rotate {
  0% {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
